<template>
    <div class="home">
        <home-header :position='city'></home-header>
        <div ref="wrapper" class="wrapper">
            <div>    
                <home-swiper :imgList="swiperImgList"></home-swiper>
                <home-icons :iconsList="iconsImgList"></home-icons>
                <home-recommend :recommendList="recommendImgList"></home-recommend>
                <home-hotlist :hostList="hotListImgList"></home-hotlist>
                <home-like :likeData="guessYouLikeData"></home-like>
                <home-weekend :weekendList="weekendImgList"></home-weekend>
            </div>
        </div>
    </div>
</template>

<script>
import HomeHeader from './components/Header'
import HomeSwiper from './components/Swiper'
import HomeIcons from './components/Icons'
import HomeRecommend from './components/Recommend'
import HomeHotlist from './components/HotList'
import HomeLike from './components/Like'
import HomeWeekend from './components/Weekend'
import BScroll from 'better-scroll'

import { mapState } from 'vuex'

export default {
    name : 'Home',
    data () {
        return {
            lastCity : '',
            swiperImgList : [],
            iconsImgList : [],
            recommendImgList : [],
            hotListImgList : [],
            guessYouLikeImgList : [],
            commentStar : '',
            weekendImgList : []
        }
    },
    components : {
        HomeHeader,
        HomeSwiper,
        HomeIcons,
        HomeRecommend,
        HomeHotlist,
        HomeLike,
        HomeWeekend
    },
    methods : {
        getHomeData () {
            fetch('/api/homeData.json?city=' + this.city) 
                .then(response => response.json())
                .then(this.dealHomeData)
                .catch(err => console.log(err))
        },
        dealHomeData (data) {
            // 获取对应的数据传递给对应的组件
            if (data.data && data.ret) {
                const res = data.data
                this.swiperImgList = res['swiperImgList']
                this.iconsImgList = res['iconsImgList']
                this.recommendImgList = res['recommendImgList']
                this.hotListImgList = res['hotListImgList']
                this.guessYouLikeImgList = res['guessYouLikeImgList']
                this.commentStar = res['commentStar']
                this.weekendImgList = res['weekendImgList']
            }
        }
    },
    // keep-alive组件生命周期函数,在切换时执行
    activated() {
        // 当城市改变时, 发送新的请求获取对应城市的数据
        if (this.lastCity !== this.city) {
            this.lastCity = this.city
            this.getHomeData()
        }
    },
    mounted() {
        // ajax请求数据
        this.getHomeData()
        // 保存城市作比较
        this.lastCity = this.city

        this.scroll = new BScroll(this.$refs.wrapper, {
            click : true
        })
    },
    computed : {
        ...mapState(['city']),

        guessYouLikeData() {
            const data = {
                guessYouLikeImgList : this.guessYouLikeImgList,
                commentStar : this.commentStar
            }
            return data
        }
    }
};
</script>

<style scoped lang="stylus">
    .wrapper    
        overflow hidden
        position absolute
        top .88rem
        left 0
        right 0
        bottom 0
</style>


