<template>
    <div class="gallery"  @click.stop="hideGallery" :style="heightStyle">
        <swiper class="wrapper" :options="swiperOption">
            <!-- slides -->
            <swiper-slide v-for="(item, index) in galleryimg" :key="index" >
                <img class="gallery-img" :src="item" >
            </swiper-slide>
            
            <!-- Optional controls -->
            <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
    name : 'Gallery',
    props : {
        galleryimg : {
            type : Array,
        }
    },
    data () {
        return {
            swiperOption : {
                pagination: {
                    el: '.swiper-pagination',
                    type : 'fraction'
                },
                observeParents : true, 
                observer : true
            }
        }
    },
    methods : {
        hideGallery() {
            this.changeGalleryState(false)
        },
        ...mapMutations({
            changeGalleryState : 'changeGalleryState'
        })
    },
    computed : {
        heightStyle () {
            return {
                height : document.documentElement.clientHeight + 'px'
            }
        }
    }
};
</script>

<style scoped lang='stylus'>
    .gallery >>> .swiper-slide
        padding-bottom 100%
    
    .gallery >>> .swiper-pagination
        bottom -1rem

    .gallery >>> .swiper-container
        overflow visible

    .gallery
        overflow hidden
        position fixed
        top 0
        left 0
        right 0
        bottom 0
        width 100%
        background-color #000
        z-index 99
        color #fff

        .wrapper
            position absolute
            left 50%
            top 50%
            transform translate(-50%, -50%)
            width 100%
            height 0
            padding-bottom 100%

            .gallery-img
                position absolute
                left 0
                top 50%
                width 100%
                transform translateY(-50%)

</style>


