<template>
    <div class="city">
        <city-header></city-header>
        <city-search :cities="cities"></city-search>
        <city-list :myCities="citiesMeg"></city-list>
        <city-alphabet :myAlphabet="alphabet"></city-alphabet>
    </div>
</template>

<script>
import CityHeader from './components/Header'
import CitySearch from './components/Search'
import CityList from './components/List'
import CityAlphabet from './components/Alphabet'

export default {
    name : 'City',
    data() {
        return {
            alphabet : [],
            citiesMeg : {},
            cities : {}
        }
    },
    components : {
        CityHeader,
        CitySearch,
        CityList,
        CityAlphabet
    },
    methods : {
        getCityData () {
            fetch('/api/city.json')
                .then(response => response.json())
                .then(this.dealCityData)
        },
        dealCityData (data) {
            // 把字母表数据传递给alphabet.vue
            if (data.data && data.ret) {
                for (var target in data.data.cities) {
                    this.alphabet.push(target)
                } 
            } 
            // 把全部数据传给list.vue
            this.citiesMeg = data.data
            this.cities = data.data.cities
        }
    },
    mounted() {
        this.getCityData()
    },
};
</script>

<style scoped lang='stylus'>

</style>


