<template>
    <div>
        <div class="detail-list" v-for="(item, index) in listData" :key="index">
            <div class="detail-list-item">
                <span class="detail-list-icon"></span>
                {{ item.title }}
            </div>
            <div v-if="item.children">
                <detail-list :listData="item.children" class="children-list"></detail-list>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name : 'DetailList',
    props : {
        listData : {
            type : Array,
            required : true
        }
    }
};
</script>

<style scoped lang='stylus'>
    .children-list
        padding 0 .2rem

    .detail-list-item
        overflow: hidden;
        position: relative;
        z-index: 4;
        margin-bottom: -.02rem;
        padding: 0 .2rem;
        height: .88rem;
        background: #fff;
        color: #333;
        font-size: .32rem;
        line-height: .88rem;
        text-indent: .4rem;

        .detail-list-icon
            position: absolute;
            width: .36rem;
            height: .36rem;
            top: .26rem;
            left: .2rem;
            background url('http://s.qunarzz.com/piao/image/touch/sight/detail.png') 0 -.45rem no-repeat
            margin-right: .1rem;
            background-size: .4rem 3rem;
    
</style>


