import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import City from '@/pages/city/City'
import Detail from '@/pages/detail/Detail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'homeLink',
      component: Home
    },
    {
      path: '/city',
      name: 'cityLink',
      component: City
    },
    {
      path: '/detail/:id',
      name: 'detailLink',
      component: Detail
    },
  ],
  mode: 'history'
})



