<template>
    <div>
        <div class="detail-banner">
            <div class="img-wrapper"  @click.stop="showGallery">
                <img class="banner-img" :src="bannerData.bannerImgUrl" alt="展览图">
            </div>
            <div class="banner-info">
                <div class="banner-number">
                    <span class="iconfont icon">&#xe613;</span>
                    <em>{{ bannerData.bannerImgNumber }}</em>
                </div>
                <div class="banner-title">{{ bannerData.bannerTitle }}</div>
            </div>
        </div>
        <fade-animate>
            <common-gallery
                v-show="clickGallery"
                :galleryimg="bannerData.galleryImg"
            ></common-gallery>
        </fade-animate>
    </div>
</template>

<script>
import commonGallery from 'common/gallery/Gallery'
import fadeAnimate from 'common/fade/fadeAnimate'
import { mapState, mapMutations } from 'vuex'

export default {
    name : 'DetailBanner',
    props : {
        bannerData : {
            type : Object,
            required : true
        }
    },
    components : {
        commonGallery,
        fadeAnimate
    },
    methods : {
        ...mapMutations({
            changeGalleryState : 'changeGalleryState'
        }),
        showGallery() {
            this.changeGalleryState(true)
        }
    },
    computed : {
        ...mapState({
            clickGallery : 'clickGallery'
        })
    }
}
</script>

<style scoped lang='stylus'>
    .detail-banner
        position relative
        height 55%
        overflow hidden

        &:after
            content ''
            position absolute
            left 0
            bottom .07rem
            height .4rem
            width 100% 
            background-image linear-gradient(to bottom,rgba(0,0,0,0),rgba(0,0,0,.8))

        .img-wrapper
            width 100%
            height 100%

            .banner-img
                width 100%

        .banner-info
            color #fff
           
            .banner-number
                position absolute
                left .2rem
                bottom .86rem
                width 1.2rem
                height .4rem 
                line-height .4rem
                border-radius .2rem
                background-color rgba(0, 0, 0, .5)
                text-align center
                font-size .24rem 

                .icon
                    font-size .24rem

            .banner-title
                position absolute
                left .2rem
                bottom .36rem
                font-size .36rem
                text-shadow: 0 1px 2px rgba(0,0,0,0.7)
</style>


