<template>
    <div class="wrapper">
        <div>
            <detail-header :headerData="headerData"></detail-header>
            <detail-banner :bannerData="bannerData"></detail-banner>
            <div class="content">
                <detail-list :listData="listData"></detail-list>
            </div>
        </div>
    </div>
</template>

<script>
import detailBanner from "./components/banner";
import detailHeader from "./components/header";
import detailList from "./components/list";

export default {
  name: "Detail",
  components: {
    detailBanner,
    detailHeader,
    detailList
  },
  data() {
    return {
      listData: [],
      bannerData : {},
      headerData : ''
    }
  },
  methods : {
    getDetailData () {
      fetch('/api/detail.json')
        .then(response => response.json())
        .then(this.dealDatailData)
    },
    dealDatailData (data) {
      if (!data.ret) {
        throw '没有数据'
      }

      const index = this.$route.params.id
      let targetData = null,
          res = data['data'],
          flag = false
  
      for (let key in res) {
        if (key === index) {
          flag = true
          targetData = res[key]
        }
      }
      
      if (!flag) {
        this.$router.push('/')
        throw "没有该推荐的详情页数据"
      }

      this.listData = targetData['listData']
      this.bannerData = targetData['bannerData']
      this.headerData = targetData['headerData']
    }
  },
  activated() {
    this.getDetailData()
  },
};
</script>

<style scoped lang='stylus'>
.wrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .content {
        width: 100%;
        height: 15rem;
    }
}
</style>


