<template>
    <div class="detail-header">

        <router-link 
            class="header-abs"
            tag="div"
            to="/"
            v-show="showAbs"
            
        >
            <span class="iconfont">&#xe624;</span>
        </router-link>

        <div class="header-opa" v-show="!showAbs" :style="opacityStyle">
            <router-link 
                class="header-back"
                tag="div"
                to="/"
            >
                <span class="iconfont">&#xe624;</span>
            </router-link>
            <span class="header-title">{{ headerData }}</span>
        </div>

    </div>
</template> 

<script>
export default {
    name : 'DetailHeader',
    props : {
        headerData : {
            type : String,
            required : true
        }
    },
    data () {
        return {
            showAbs : true,
            // 动态绑定透明度
            opacityStyle : {
                opacity : 0
            }
        }
    },
    methods : {
        handleScroll () {
            let top = document.documentElement.scrollTop ||  document.body.scrollTop
            // alert(top)
            if (top > 0) {
                let opacity = top / 100
                // 确保透明度最大为 1
                opacity = opacity > 1 ? 1 : opacity
                this.opacityStyle = { opacity }
                this.showAbs = false
            } else {
                this.showAbs = true
            }
        }
    },
    activated() {
        // keep-alive生命周期函数监听滚动事件
        window.addEventListener('scroll', this.handleScroll, false)

        // 监听鼠标滚轮事件
        // window.addEventListener('DOMMouseScroll', this.handleScroll, false)
        // window.addEventListener('mousewheel', this.handleScroll, false)
    },
    deactivated() {
        // 解除全局事件
        window.removeEventListener('scroll', this.handleScroll, false)
        // window.removeEventListener('DOMMouseScroll', this.handleScroll, false)
        // window.removeEventListener('mousewheel', this.handleScroll, false)
    },
};
</script>

<style scoped lang='stylus'>
    @import '~styles/varible'
    .detail-header
        position relative

        .header-abs
            position absolute 
            left .1rem
            top .1rem
            width .75rem
            height .75rem
            border-radius 50%
            background-color rgba(0, 0, 0, .5)
            text-align center
            line-height .7rem
            color #fff
            font-weight bolder
            z-index 1

        .header-opa
            position fixed 
            top 0
            left 0
            height .88rem
            width 100%
            line-height .88rem
            background-color $bgColor
            color #fff
            z-index 99

            .header-back
                position absolute
                top 50%
                left .25rem
                transform translateY(-50%)
                font-weight bolder
                font-size .35rem

            .header-title
                position absolute
                top 50%
                left 50%
                transform translate(-50%, -50%)
                font-size .32rem


        
</style>


